.c-contract-list {
  width: 100%; }
  .c-contract-list .c-spinner {
    position: absolute;
    left: 12px;
    top: 24px; }

.c-contract-list__top {
  box-sizing: border-box;
  position: relative;
  padding: 10px 20px 10px 80px;
  min-height: 52px;
  border-bottom: 2px #ea650d solid;
  font-size: 24px;
  line-height: 28px;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  color: #ea650d;
  overflow: hidden; }

.c-contract-list__icon {
  position: absolute;
  top: 50%;
  left: 20px;
  margin-top: -16px;
  font-size: 32px;
  line-height: 1; }

.c-contract-list__item-wrapper {
  position: relative;
  border-bottom: 1px #cac7c7 solid; }
  .c-contract-list__item-wrapper:last-child {
    border-bottom: 0; }

.c-contract-list__item {
  display: block;
  width: 100%;
  height: auto;
  box-sizing: border-box;
  margin: 0;
  padding: 10px 15px 10px 75px; }
  .c-contract-list__item, .c-contract-list__item:link, .c-contract-list__item:visited, .c-contract-list__item:hover, .c-contract-list__item:active, .c-contract-list__item:focus {
    border: 0;
    text-decoration: none;
    font-weight: normal;
    font-family: inherit;
    line-height: inherit;
    background: transparent;
    color: #414141; }
  .c-contract-list__item:hover, .c-contract-list__item:active {
    background-color: #f1edeb; }
  .c-contract-list__item:hover .c-contract-list__item__title:after, .c-contract-list__item:active .c-contract-list__item__title:after, .c-contract-list__item:focus .c-contract-list__item__title:after {
    transform: translateX(5px); }
  .c-contract-list__item:after {
    content: "";
    display: table;
    clear: both; }
  .c-contract-list__item--inactive {
    cursor: default; }
    .c-contract-list__item--inactive .c-contract-list__item__title:after {
      color: #999999; }
  .c-contract-list__item--no_details:hover, .c-contract-list__item--no_details:active {
    background-color: transparent; }
  .c-contract-list__item--no_details .c-contract-list__item__title:after {
    display: none; }
  .c-contract-list__item__action {
    margin: 5px 15px 10px 5px; }
    .c-contract-list__item__action:first-child {
      margin-left: 0; }

.c-contract-list__item__title {
  position: relative;
  display: block;
  font-size: 20px;
  padding-right: 20px;
  overflow-wrap: break-word;
  font-weight: bold;
  -webkit-font-smoothing: antialiased; }
  .c-contract-list__item__title:after {
    position: absolute;
    display: inline-block;
    vertical-align: top;
    font-family: 'nn-iconfont' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: ">";
    color: #ea650d;
    padding-left: 0.5em;
    transition: transform 0.3s; }

.c-contract-list__item__content {
  display: block;
  width: 100%; }

.c-contract-list__item__status {
  display: inline-block;
  vertical-align: top;
  padding: 7px 12px;
  border: 1px currentColor solid;
  background-color: white;
  font-size: 18px;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  border-radius: 20px;
  text-align: center; }

.c-contract-list__item__content,
.c-contract-list__item__status {
  margin: 10px 5px;
  box-sizing: border-box; }
